
html{
  scroll-behavior: smooth;
}

.container.wrap {
  margin: 25px auto;
  -webkit-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
  -moz-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
  box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
}

.section {
  overflow: hidden !important;
  position: relative;
  min-height: 100vh;
  margin-left: -15px;
  margin-right: -15px;
}

.nopadding {
  padding: 0;
}

h1.caption {
  font-size: 2rem;
  font-family: 'Play', sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 600;
  margin: 0 -15px;
  padding: 0.6em 0;
  background-color: #eee;
}

h3.caption {
  font-weight: 600;
  font-size: 1.5rem;
  padding: 0.6em 0;
}

