/* Section: Contacts */
.contacts{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  & h3{
    font-size: 1.5rem;
  }

  &__btns{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }
}

.contacts-body{
  height: 450px;
  overflow: hidden;
  font-family: 'Play', sans-serif;

  &__image{
    min-width: 200px;
    background: url(../images/znak.png) no-repeat center; 
    background-size: 100%;
  }
  
  &__requisites{
    position: relative;
  }

  &__submit-form{
    position: relative;
    display: none;
  }

  &__list{
    list-style-type: none;
    & li{
      margin-left: -20px;
      font-size: 1.2rem;
    }
  }
}

.contacts-footer{
  &__text{
    font-family: 'Play', sans-serif;
    font-size: 1.2rem;
  }

  &__text::before{
    content:'';
    background: url(../images/check-mark-box.svg) no-repeat;
    background-size: 100%;
    margin-right: 18px;
    padding: 16px;
  }
}

label {
  font-size: 1.2rem !important;
  padding-bottom: 14px;
}

input, textarea{
  font-size: 1.2rem !important;
  border-radius: 0 !important;
}

.btn{
  color: #fff !important;
  border-radius: 0;
  font-size: 1.2rem;
  padding: 0.4em 1.8em;
  &.btn-danger{
    background: rgb(212,75,56);
    border: 1px solid rgb(212,75,56);
  }
  &.btn-primary{
    background: rgb(39, 46, 56);
    border: 1px solid rgb(39, 46, 56);
  }
  &:hover{
    opacity: .8;
  }
}


@import './desktop/width-992.scss';
